.preloader {
  margin: 0;
  min-height: 100%;
  background-color: #4b72b4;
  border-radius: 50px;
  width: 100%;
  overflow: hidden;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.circle-preloader {
  display: block;
  width: 50px;
  height: 50px;
  border: 4px solid #000;
  border-bottom-color: #fff;
  border-radius: 50%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  animation: spin 0.75s infinite linear;
}

@keyframes spin {
  100% {
    transform: rotate(360deg);
  }
}
